const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list:[],
			type: '',
			imgUrl: '',
			token: ''
		}
	},
	onLoad(options) {
		console.log(options)
		this.token = uni.getStorageSync('token')
		uni.setNavigationBarTitle({
			title: options.title
		})
		this.type = options.type
		this.getList()
		this.imgUrl = helper.imgUrl
	},
	methods: {
		getList(){
			let url = ''
			if(this.type == '1'){
				url='abouts'
			} else if(this.type == '2'){
				url='comments'
			} else if(this.type == '3'){
				url='news-list'
			}
			uni.request({
				url: `${helper.requestUrl}/user/${url}`,
				method: 'GET',
				header: {
					authorization: this.token
				},
				// data: {
				// 	page: 1,
				// 	page_size: 5
				// },
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.list = res.data.data;
					} else {
						// uni.showToast({
						// 	title: res.data.message
						// });
					}
				}
			});
		}
	}
}
